.new-todo-form {
	display: flex;
	margin-bottom: 0;
}

.new-todo,
.new-todo-form .new-todo-form__timer {
	position: relative;
	margin: 0;
	width: 100%;
	font-size: 24px;
	font-family: inherit;
	font-weight: inherit;
	line-height: 1.4em;
	color: inherit;
	padding: 6px;
	border: 1px solid #999;
	box-shadow: inset 0 -1px 5px 0 rgba(0, 0, 0, 0.2);
	box-sizing: border-box;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.new-todo, .new-todo-form .new-todo-form__timer {
	padding: 16px 16px 16px 60px;
	border: none;
	background: rgba(0, 0, 0, 0.003);
	box-shadow: inset 0 -2px 1px rgba(0,0,0,0.03);
}

.new-todo {
	width: 70%;
}

.new-todo-form .new-todo-form__timer {
	width: 15%;
	padding-left: 16px;
}

.new-todo-form input[type="number"]::-webkit-inner-spin-button,
.new-todo-form input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}